import { Injectable } from '@angular/core';
import { AngularFirestore } from "@angular/fire/firestore";
import { AuthService } from "./auth.service";

@Injectable({
  providedIn: 'root'
})
export class FirestoreService {

  constructor(private firestore:AngularFirestore, private auth:AuthService) {

   }

  insert(collection_name:string,data:any):any{
    return this.firestore.collection(collection_name).add(data);
  }

  getDataByColumnName(collection_name:string,column_name:string,data:string):any{
    const db=this.firestore
    .collection(collection_name,query=>query.where(column_name,"==",data))
    .snapshotChanges();
    return db;

  }

    getDataById(collection_name:string,id:string):any{
      const db=this.firestore.collection(collection_name).doc(id).valueChanges();
      return db;
    }

  deleteData(collection_name:string,id:string):any{
      const db=this.firestore.collection(collection_name).doc(id).delete();
      return db;
    }

  updateData(collection_name:string,id:string,data:any):any{
      const db=this.firestore.collection(collection_name).doc(id).update(data);
      return db;
    }
}
