import { Component, OnInit, Inject } from '@angular/core';
import {MatSnackBarRef, MAT_SNACK_BAR_DATA} from "@angular/material/snack-bar";
import { FirestoreService } from "../services/firestore.service";
import { CallerService } from "../services/caller.service";

@Component({
  selector: 'app-snackbar',
  templateUrl: './snackbar.component.html',
  styleUrls: ['./snackbar.component.scss']
})
export class SnackbarComponent implements OnInit {

  public h:number=0;
  public m:number=0;
  public s:number=0;
  constructor(
    @Inject(MAT_SNACK_BAR_DATA) public snackbarData:any,
    private firestore:FirestoreService,
    private snackbar:MatSnackBarRef<SnackbarComponent>,
    private caller:CallerService
  ) { }

  ngOnInit(): void {
    this.stopwatch();
  }

  stopwatch():void{
    setInterval(()=>{
      this.s++;
      if(this.s==60)
      {
        this.s=0;
        this.m++;
      }
      if(this.m==60)
      {
        this.m=0;
        this.h++;
      }
    },1000);
  }

  endCall(notes:any):void{
    const data={
      notes:notes.value.trim(),
      updated_at:new Date()
    };
    const logs = this.firestore.updateData("logs",this.snackbarData.lastInsertedId,data);
    logs.then(()=>{
      this.snackbar.dismiss();
      this.snackbar.afterDismissed().subscribe(()=>{
        this.caller.triggerWithData(this.snackbarData);
      });
    });
  }
}
