import { Injectable } from '@angular/core';
import {Subject} from "rxjs";

@Injectable({
  providedIn: 'root'
})
export class CallerService {

  constructor() { }
  public subject=new Subject();
  trigger():void{
    this.subject.next();
  }
  triggerWithData(data:any):void{
    this.subject.next(data);
  }
}
