import { Injectable } from '@angular/core';
import { AngularFireAuth } from "@angular/fire/auth";
import { AngularFirestore } from "@angular/fire/firestore";
import { Router } from "@angular/router";
import { CookieService } from 'ngx-cookie-service';
import Swal from 'sweetalert2';



@Injectable({
  providedIn: 'root'
})
export class AuthService {

  constructor(private cookie:CookieService, private router:Router, private auth:AngularFireAuth, private firestore:AngularFirestore) {
      this.checkAuthState();
   }

  emailSignup(formdata:any):void{
    this.auth.createUserWithEmailAndPassword(formdata.email,formdata.password).then((response:any)=>{
      const user_id=response.user.uid;
      const data={
        fullname:formdata.fullname,
        phone:formdata.phone,
        website:formdata.website
      };
      this.storeUserInfo(user_id, data);
    })
    .catch((error:any)=>{
      console.log(error);
    });
  }

  private  storeUserInfo(user_id:string, data:any):void{
    this.firestore.collection("users").doc(user_id).set(data);
  }

  checkAuthState():void{
    this.auth.authState.subscribe((response:any)=>{
      if(response !=null)
      {
        if(response.emailVerified)
        {
          this.cookie.set("user_id",response.uid);
          this.router.navigateByUrl("/profile/logs");
        }
        else
        {
          response.sendEmailVerification().
          then(()=>{
            const icon="success";
            const title="Verification Required !";
            const text="You have to verify your email address check your email id for verification link";
            this.alert(icon,title,text);
          });

        }

      }
      else
      {
        this.router.navigateByUrl("");
      }
    });
  }

  signOut(): void{
    this.auth.signOut().then(()=>{
      console.log("Signout Done");
    });
  }
  emailSignIn(formdata:any):void{
    this.auth.signInWithEmailAndPassword(formdata.email,formdata.password);
  }

  alert(icon:any,title:any,text:any):void{
        Swal.fire({
          icon: icon,
          title: title,
          text: text,
          confirmButtonText:"Done",
          showClass:{
            popup:"animate__animated animate__zoomIn",
            icon:"animate__animated animate__rotateIn"
          },
          hideClass:{
            popup:"animate__animated animate__zoomOut",
            icon:"animate__animated animate__rotateOut"
          }
        })
  }

  isUserAlreadyExists(email:string):any{
    return this.auth.fetchSignInMethodsForEmail(email);

  }
}
